import { useState, useEffect } from 'react';

import { useSelector } from 'react-redux';
import useApiService from './../../../service/ApiService';

import './myOrderPage.scss'

const MyOrderPage = () => {
  
  const { user } = useSelector(state => state);
  const { getMyOrders } = useApiService();

  const [items, setItems] = useState([])
  const [error, setError] = useState(false)

  useEffect(() => {
    getMyOrders(user.id)
    .then((res) => {
      if (res.status !== 'bad_data') {
        console.log(res.data);
        setItems(res.data);
      }
    }).catch(() => {
      setError(true);
    })
  }, [])

  const errorMessage = error ? <span className="mt-2 col-md-8" style={{color: 'red'}}
  >.        </span> : null;

  return (
    <div className="site-section">
      <div className="container">
        <div className="row mb-5 align-items-center">
          <div className="col-md-7">
            <h2 className="heading-39291 mb-0"> </h2>
          </div>
        </div>
        <ItemsOrder items={items}/>
        {errorMessage}
      </div>
    </div>
  );
};

const ItemsOrder = ({items}) => {

  const arr = items.map((item, i) => {
    return (
      <div key={i} className="order-form-item row mb-5">
        <p className="col-sm-12"><strong> : {item.key}</strong></p>
        <div className="order-group col-sm-4">
          <span className="mb-2">:</span>
          <span>C: {item.from_country}</span>
          <span>: {item.from_city}</span>
          <span>: {item.from_zip}</span>
          <span>: {item.from_address}</span>
          <span>: {item.from_name}</span>
          <span>: {item.from_phone}</span>
        </div>
        <div className="order-group col-sm-4">
          <span className="mb-2">:</span>
          <span>: {item.to_country}</span>
          <span>: {item.to_city}</span>
          <span>: {item.to_zip}</span>
          <span>: {item.to_address}</span>
          <span>: {item.to_name}</span>
          <span>: {item.to_phone}</span>
        </div>
        <div className="order-group col-sm-3">
          <span className="mb-2"> :</span>
          <span>: {item.weight}</span>
          <span>: {item.volume}^3</span>
          <span>: {item.status}</span>
          <span>: {item.price}</span>
        </div>
      </div>
    )
  })

  return (
    <form className="order-form row">
      {arr}
    </form>
  ) 
}

export default MyOrderPage;